/* Simplified App Styling */
:root {
  --text-color: #333;
  --gray-color: #808080;
  --link-color: gray;
  --header-height: 60px;
}

body {
  font-family: Cochin, Georgia, 'Times New Roman', serif;
  color: var(--text-color);
  background-color: white;
  margin: 0;
  padding: 0;
  line-height: 1.4;
}

.App {
  text-align: center;
  margin-top: 60px;
  height: calc(100vh - 60px);
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

/* Header styling */
.modern-header {
  height: var(--header-height);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 2rem;
  background-color: white;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  border-bottom: 1px solid #f0f0f0;
}

.logo {
  font-weight: 400;
  font-size: 1.2rem;
  color: var(--text-color);
}

.modern-nav {
  display: flex;
  gap: 5rem; /* More spacing between nav items like original */
}

.nav-link {
  text-decoration: none;
  color: var(--link-color);
  font-weight: normal;
  padding: 0.5rem 0;
}

.nav-link:hover {
  color: #444;
}

.modern-main {
  flex: 1;
  margin-top: var(--header-height);
  padding: 1rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  overflow-y: auto;
}

.modern-footer {
  padding: 1rem;
  text-align: center;
  font-size: 0.8rem;
  color: var(--gray-color);
}

/* Content containers */
.modern-container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 0 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.intro-section {
  text-align: center;
  padding: 1rem 0;
}

.modern-title {
  font-size: 1.5em;
  margin-bottom: 0.5rem;
  color: black;
}

.modern-subtitle {
  font-size: large;
  color: var(--text-color);
  margin: 0 0 1rem;
}

.highlight-text {
  font-size: medium;
  color: var(--text-color);
  margin-bottom: 1rem;
}

/* ChatBot section */
.chatbot-section {
  width: 100%;
  max-width: 700px;
  margin: 0 auto;
  background-color: white;
  border: 1px solid #f0f0f0;
  height: 400px; /* Fixed height to avoid scrolling */
}

/* Below sections */
.below-sections {
  margin-top: 1rem;
  display: flex;
  justify-content: center;
  gap: 2rem;
}

/* CV route */
.CV-route {
  height: calc(100vh - 60px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: auto;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .App {
    margin-top: 50px;
    height: calc(100vh - 50px);
  }
  
  .modern-header {
    padding: 0 1rem;
    height: 50px;
  }
  
  .below-sections {
    flex-direction: column;
    gap: 1rem;
  }
  
  .modern-title {
    font-size: 1.3em;
  }
  
  .chatbot-section {
    height: 350px;
  }
  
  .modern-nav {
    gap: 2rem;
  }
}

@media (max-width: 480px) {
  .chatbot-section {
    height: 300px;
  }
}

/* Legacy compatibility styles */
.App-header {
  font-family: Cochin, Georgia, 'Times New Roman', serif;
  padding: 1.5rem 1rem;
  height: auto;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-family: Cochin, Georgia, 'Times New Roman', serif;
  font-size: large;
}

.Links {
  font-family: Cochin, Georgia, 'Times New Roman', serif;
  padding: 2rem;
  text-decoration: none;
}
