body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

/* Simplified App Styling */
:root {
  --text-color: #333;
  --gray-color: #808080;
  --link-color: gray;
  --header-height: 60px;
}

body {
  font-family: Cochin, Georgia, 'Times New Roman', serif;
  color: #333;
  color: var(--text-color);
  background-color: white;
  margin: 0;
  padding: 0;
  line-height: 1.4;
}

.App {
  text-align: center;
  margin-top: 60px;
  height: calc(100vh - 60px);
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

/* Header styling */
.modern-header {
  height: 60px;
  height: var(--header-height);
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 2rem;
  background-color: white;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  border-bottom: 1px solid #f0f0f0;
}

.logo {
  font-weight: 400;
  font-size: 1.2rem;
  color: #333;
  color: var(--text-color);
}

.modern-nav {
  display: flex;
  grid-gap: 5rem;
  gap: 5rem; /* More spacing between nav items like original */
}

.nav-link {
  text-decoration: none;
  color: gray;
  color: var(--link-color);
  font-weight: normal;
  padding: 0.5rem 0;
}

.nav-link:hover {
  color: #444;
}

.modern-main {
  flex: 1 1;
  margin-top: 60px;
  margin-top: var(--header-height);
  padding: 1rem 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  overflow-y: auto;
}

.modern-footer {
  padding: 1rem;
  text-align: center;
  font-size: 0.8rem;
  color: #808080;
  color: var(--gray-color);
}

/* Content containers */
.modern-container {
  max-width: 1000px;
  margin: 0 auto;
  padding: 0 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.intro-section {
  text-align: center;
  padding: 1rem 0;
}

.modern-title {
  font-size: 1.5em;
  margin-bottom: 0.5rem;
  color: black;
}

.modern-subtitle {
  font-size: large;
  color: #333;
  color: var(--text-color);
  margin: 0 0 1rem;
}

.highlight-text {
  font-size: medium;
  color: #333;
  color: var(--text-color);
  margin-bottom: 1rem;
}

/* ChatBot section */
.chatbot-section {
  width: 100%;
  max-width: 700px;
  margin: 0 auto;
  background-color: white;
  border: 1px solid #f0f0f0;
  height: 400px; /* Fixed height to avoid scrolling */
}

/* Below sections */
.below-sections {
  margin-top: 1rem;
  display: flex;
  justify-content: center;
  grid-gap: 2rem;
  gap: 2rem;
}

/* CV route */
.CV-route {
  height: calc(100vh - 60px);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow: auto;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .App {
    margin-top: 50px;
    height: calc(100vh - 50px);
  }
  
  .modern-header {
    padding: 0 1rem;
    height: 50px;
  }
  
  .below-sections {
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
  }
  
  .modern-title {
    font-size: 1.3em;
  }
  
  .chatbot-section {
    height: 350px;
  }
  
  .modern-nav {
    grid-gap: 2rem;
    gap: 2rem;
  }
}

@media (max-width: 480px) {
  .chatbot-section {
    height: 300px;
  }
}

/* Legacy compatibility styles */
.App-header {
  font-family: Cochin, Georgia, 'Times New Roman', serif;
  padding: 1.5rem 1rem;
  height: auto;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-family: Cochin, Georgia, 'Times New Roman', serif;
  font-size: large;
}

.Links {
  font-family: Cochin, Georgia, 'Times New Roman', serif;
  padding: 2rem;
  text-decoration: none;
}

/* Simplified CV/Resume Component Styles */
.cv-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 850px;
  margin: 0 auto;
  padding: 1rem;
  font-family: Cochin, Georgia, 'Times New Roman', serif;
  height: calc(100vh - 60px);
}

.cv-loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 300px;
  width: 100%;
  color: #666;
}

.cv-spinner {
  width: 30px;
  height: 30px;
  margin-bottom: 1rem;
  border: 2px solid rgba(0, 0, 0, 0.1);
  border-top: 2px solid #666;
  border-radius: 50%;
  -webkit-animation: spin 1s linear infinite;
          animation: spin 1s linear infinite;
}

@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.cv-error {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 200px;
  width: 100%;
  color: #c00;
  background-color: #fff;
  border: 1px solid #eee;
  padding: 1rem;
  text-align: center;
}

.cv-document {
  width: 100%;
  height: auto;
  margin-bottom: 1rem;
  border: 1px solid #eee;
  background-color: white;
  display: flex;
  justify-content: center;
  overflow: auto;
}

.pdf-document {
  width: 100%;
  display: flex;
  justify-content: center;
}

.pdf-page {
  margin: 0;
  box-shadow: none !important;
}

.pdf-page canvas {
  margin: 0 auto !important;
  max-width: 100% !important;
  height: auto !important;
}

.cv-controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0.5rem;
  background-color: white;
  border: 1px solid #eee;
  margin-top: 0.5rem;
  flex-wrap: wrap;
  grid-gap: 1rem;
  gap: 1rem;
}

.cv-pagination {
  display: flex;
  align-items: center;
  grid-gap: 0.5rem;
  gap: 0.5rem;
}

.cv-page-info {
  font-size: 0.875rem;
  color: #333;
}

.cv-zoom {
  display: flex;
  align-items: center;
  grid-gap: 0.5rem;
  gap: 0.5rem;
}

.cv-zoom-level {
  font-size: 0.875rem;
  color: #333;
  min-width: 3rem;
  text-align: center;
}

.cv-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 28px;
  height: 28px;
  background-color: #f8f8f8;
  border: 1px solid #ddd;
  color: #333;
  cursor: pointer;
}

.cv-button:hover {
  background-color: #eee;
}

.cv-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.cv-download-button {
  display: flex;
  align-items: center;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  padding: 0.3rem 0.7rem;
  background-color: #f8f8f8;
  border: 1px solid #ddd;
  color: #333;
  font-size: 0.875rem;
  text-decoration: none;
}

.cv-download-button:hover {
  background-color: #eee;
}

@media (max-width: 768px) {
  .cv-container {
    padding: 0.5rem;
    height: calc(100vh - 50px);
  }
  
  .cv-controls {
    flex-direction: column;
    align-items: center;
    grid-gap: 0.5rem;
    gap: 0.5rem;
  }
  
  .cv-pagination, .cv-zoom {
    width: 100%;
    justify-content: center;
  }
}
/* Simplified About Component Styles */
.About {
  text-align: center;
  padding-bottom: 20px;
  max-width: 500px;
}

.About-header {
  font-family: Cochin, Georgia, 'Times New Roman', serif;
  height: auto;
  padding: 10px;
}

.About-title {
  font-size: 1.5em;
  font-weight: normal;
  margin-bottom: 10px;
}

.About-intro {
  font-family: Cochin, Georgia, 'Times New Roman', serif;
  font-size: large;
  line-height: 1.4;
  color: #333;
}

@media (max-width: 768px) {
  .About {
    padding-bottom: 15px;
  }
  
  .About-intro {
    font-size: medium;
  }
}

/* Simplified Contact Component Styles */
.Contact {
  text-align: center;
  max-width: 500px;
}

.Contact-header {
  font-family: Cochin, Georgia, 'Times New Roman', serif;
  height: auto;
  padding: 10px;
}

.Contact-title {
  font-size: 1.5em;
  font-weight: normal;
  margin-bottom: 10px;
}

.Contact-intro {
  font-family: Cochin, Georgia, 'Times New Roman', serif;
  font-size: large;
  line-height: 1.4;
  margin: 0.5rem 0;
  color: #333;
}

@media (max-width: 768px) {
  .Contact-intro {
    font-size: medium;
  }
}

/* Simplified Chatbot Component Styles */
.chatbot-container {
  display: flex;
  flex-direction: column;
  height: 400px; /* Reduced height to avoid scrolling */
  width: 100%;
  margin: 0 auto;
  background-color: white;
  border: 1px solid #e5e7eb;
  overflow: hidden;
}

.chatbot-header {
  padding: 10px 15px;
  background-color: #f8f8f8;
  border-bottom: 1px solid #e5e7eb;
}

.chatbot-title {
  display: flex;
  align-items: center;
  grid-gap: 8px;
  gap: 8px;
  font-weight: normal;
  color: #333;
  font-family: Cochin, Georgia, 'Times New Roman', serif;
}

.chatbot-title svg {
  color: gray;
}

.chatbot-messages {
  flex: 1 1;
  padding: 15px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  grid-gap: 12px;
  gap: 12px;
  scroll-behavior: smooth;
  background-color: white;
}

.message {
  display: flex;
  align-items: flex-start;
  max-width: 85%;
}

.message.user {
  margin-left: auto;
  justify-content: flex-end;
}

.message.bot {
  margin-right: auto;
}

.avatar {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  margin-right: 8px;
  color: gray;
}

.message-bubble {
  padding: 8px 12px;
  background-color: #f8f8f8;
  color: #333;
  border: 1px solid #e5e7eb;
  line-height: 1.4;
  font-family: Cochin, Georgia, 'Times New Roman', serif;
}

.message.user .message-bubble {
  background-color: #e5e7eb;
  color: #333;
  border: none;
}

.chatbot-input {
  display: flex;
  align-items: center;
  padding: 10px 15px;
  background-color: white;
  border-top: 1px solid #e5e7eb;
}

.chat-input {
  flex: 1 1;
  padding: 8px 12px;
  border: 1px solid #e5e7eb;
  background-color: white;
  font-size: 14px;
  font-family: Cochin, Georgia, 'Times New Roman', serif;
}

.chat-input:focus {
  outline: none;
  border-color: gray;
}

.chat-send-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  margin-left: 8px;
  background-color: #f0f0f0;
  color: #333;
  border: 1px solid #e5e7eb;
  cursor: pointer;
}

.chat-send-button:hover {
  background-color: #e0e0e0;
}

.chat-send-button:disabled {
  background-color: #f8f8f8;
  color: #ccc;
  cursor: not-allowed;
}

.chatbot-footer {
  padding: 8px;
  background-color: #f8f8f8;
  border-top: 1px solid #e5e7eb;
  font-size: 12px;
  color: gray;
  text-align: center;
  font-family: Cochin, Georgia, 'Times New Roman', serif;
}

/* Typing indicator */
.typing-indicator {
  display: flex;
  align-items: center;
  background-color: #f0f0f0;
  padding: 8px 12px;
}

.typing-indicator span {
  height: 6px;
  width: 6px;
  background-color: #888;
  border-radius: 50%;
  margin: 0 2px;
  display: inline-block;
  opacity: 0.6;
  -webkit-animation: dot-pulse 1.5s infinite ease-in-out;
          animation: dot-pulse 1.5s infinite ease-in-out;
}

.typing-indicator span:nth-child(2) {
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s;
}

.typing-indicator span:nth-child(3) {
  -webkit-animation-delay: 0.4s;
          animation-delay: 0.4s;
}

@-webkit-keyframes dot-pulse {
  0%, 100% { 
    opacity: 0.6;
  }
  50% { 
    opacity: 1;
  }
}

@keyframes dot-pulse {
  0%, 100% { 
    opacity: 0.6;
  }
  50% { 
    opacity: 1;
  }
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .chatbot-container {
    height: 350px;
  }
  
  .message {
    max-width: 90%;
  }
}

@media (max-width: 480px) {
  .chatbot-container {
    height: 300px;
  }
  
  .chatbot-header,
  .chatbot-input {
    padding: 8px 12px;
  }
  
  .chat-input {
    font-size: 13px;
  }
}
